.InfoContainer{
    margin-top: 13vh;
    height:300px;
    z-index: 1;
}
.InfoContainer-headertext{
    font-weight: 700;
    line-height: 58px;
    font-size: 50px;
}
.InfoContainer-text{
    padding-top: 20px;
    font-size: 28px;
}
@media screen and (max-width: 1100px) {
    .InfoContainer-headertext{
        line-height: normal;
        font-size: 40px;
    }
    .InfoContainer-text{
        font-size: 28px;
    }
  }
  @media screen and (max-width: 800px) {
    .InfoContainer-headertext{
        line-height: normal;
        font-size: 34px;
    }
    .InfoContainer-text{
        font-size: 25px;
    }
  }

  @media screen and (max-width: 600px) {
    .InfoContainer-headertext{
        line-height: normal;
        font-size: 31px;
    }
    .InfoContainer-text{
        font-size: 23px;
    }
  }