.NavBarButtonRedirect{
    display:flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    height:50px;
    min-width: 100px;
    background-color: #FA5B35;
    transition-duration: 0.2s;
    border: none;
}
.NavBarButtonRedirect:hover{
    cursor: pointer;
    background-color: rgba(255, 157, 133, 0.706);
    
}
.NavBarButtonRedirect-png{
    width:20px;
    padding-left: 20px;
}
.NavBarButtonRedirect-text{
    font-size: 18px;
    padding-right: 20px;
}

@media screen and (max-width: 700px) {
    .NavBarButtonRedirect{
        height:40px;
        min-width: 80px;
    }
    .NavBarButtonRedirect-png{
        padding-left: 15px;
        width:15px
    }
    .NavBarButtonRedirect-text{
        padding-right: 15px;
        font-size: 14px;
    }
  }