.LoginBox{
    pointer-events: all;
    position: absolute;
    left: calc(50vw - 250px);
    top: calc(50vh - 300px);
    z-index: 1001;
    width: 500px;
    height: 600px;
    border-radius: 14px;
    background: white;
}

.ExitButtonContainer{
    width:100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.inputcontainer-login{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 550px;
}

.LoginBox-BottomContainer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 413px;
    align-items: center;
    margin-bottom: 20px;
}