.InfoBoxContainer{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 10vh;
    z-index: 1;
}

@media screen and (max-width: 800px) {
    .InfoBoxContainer{
        flex-direction: column;
        align-items: center;
        padding-top: 70vh;
        gap: 5vh;
    }
  }