.InfoSection{
    display:flex;
    flex-direction: column;
    background-color: #FFFFFF;
    gap: 10vh;
    padding-bottom: 10vh;
}

.info-img{
    position: absolute;
    background: transparent;
    width:350px;
    margin-left: 45vw;
    z-index: 0;
    padding-top: 3vh;
}

@media screen and (max-width: 1300px) {
    .info-img{
        margin-left: 50vw;
    }
  }

@media screen and (max-width: 800px) {
    .info-img{
        margin-left: 0px;
        margin-top: 45vh;

    }
  }